.chart-point {
	display: flex;
	align-items: center;

	.check-point-area {
		width: 100px;
		height: 100px;
		margin-top: -10px;
		margin-left: -10px;
	}

	.chart-point-list {
		margin: 0;
		padding-left: 5px;

		li {
			list-style: none;
			font-size: 13px;
			padding: 2px 0;

			i {
				margin-right: 5px;
				font-size: 11px;
				position: relative;
				top: -1px;
			}
		}
	}
}