.authincation-content {
    background: $primary;
    box-shadow: 0 0 35px 0 rgba(154, 161, 171, 0.15);
    border-radius: 5px;

    @at-root [data-theme-version="dark"] & {
        background: $dark-card;
        box-shadow: none;
    }

}

.welcome-content {
    background: url('./../images/1.jpg');
    background-size: cover;
    background-position: center;
    height: 100%;
    padding: 75px 50px;
    position: relative;
    z-index: 1;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;

    &::after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: $primary;
        opacity: 0.75;
        z-index: -1;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
    }

    .welcome-title {
        color: $white;
        font-weight: 500;
        font-size: 20px;
    }

    p {
        color: $white;
    }

    .brand-logo {
        a {
            display: inline-block;
            margin-bottom: 20px;
            font-weight: 700;
            color: $white;
            font-size: 20px;

            img {
                width: 100px;
            }
        }
    }

    .intro-social {
        position: absolute;
        bottom: 75px;

        ul {
            margin-bottom: 0;

            li {
                display: inline-block;

                a {
                    color: $white;
                    font-size: 14px;
                    padding: 0px 7px;
                }
            }
        }
    }
}

.auth-form {
    padding: 50px 50px;

    @include respond('phone') {
        padding: 30px;
    }

    .btn {
        height: 50px;
        font-weight: 700;
    }

    .page-back {
        display: inline-block;
        margin-bottom: 15px;
    }
}