.email-left-box {
    width: 15rem;
    float: left;
    padding: 0 1.25rem 1.25rem 1rem;
    border-top: 0;
    border-left: 0;

    @include media-breakpoint-between(sm, md) {
        width: 100%;
        padding-bottom: 0px !important;
    }

    @include media-breakpoint-down(xs) {
        width: 100%;
        float: none;
        border: none;
        padding-bottom: 30px !important;
    }

    .intro-title {
        background: rgba($primary, .1);
        padding: 1rem;
        margin: 1.875rem 0 1.25rem 0;

        h5 {
            margin-bottom: 0;
            color: #6a707e;
            font-size: 14px;

            i {
                font-size: 0.75rem;
                position: relative;
                bottom: 1px;
            }
        }

        i {
            color: $primary;

        }
    }
}

.email-right-box {
    padding-left: 15rem;
    padding-right: 1rem;

    @include media-breakpoint-between(sm, md) {
        padding-left: 0;
        margin-left: 0 !important;
    }

    @include media-breakpoint-down(xs) {
        padding-left: 0;
        padding-right: 0;
    }

    .right-box-border {
        border-right: 2px solid rgba($primary, .1);
    }

    .right-box-padding {

        @media screen and (min-width: 649px) and (max-width: 1200px) {
            padding-left: 1.25rem;
        }

        @media(min-width: 1700px) {
            padding-left: 0.9375rem;
        }

        @include media-breakpoint-between(sm, md) {
            padding-left: 0;
        }
    }
}

.toolbar {
    .btn-group {
        .btn {
            border: 0;
        }

        input {
            position: relative;
            top: 2px;
        }
    }
}

.read-content {
    textarea {
        height: 150px;
    }

    &-email {
        font-size: 0.875rem;
    }

    h5 {
        color: #6a707e;
    }

    p {

        strong {
            color: #6a707e;
        }
    }

    &-body {

        p {
            margin-bottom: 1.875rem;
        }
    }

    &-attachment {
        padding: 0.5rem 0;

        h6 {
            font-size: 1.125rem;
            color: #6a707e;

            i {
                padding-right: 0.3125rem;
            }
        }

        .attachment {

            &>div:not(:last-child) {
                border-right: 1px solid #DDDFE1;
            }
        }
    }
}

.compose-content {
    .wysihtml5-toolbar {
        border-color: $l-border;
    }

    .dropzone {
        background: $l-bg !important;
    }

    h5 {
        font-size: 1.0625rem;
        color: #6a707e;

        i {
            font-size: 1.125rem;
            transform: rotate(90deg);
        }
    }

    .dropzone {
        border: 1px dashed #DDDFE1;
        min-height: 13.125rem;
        position: relative;

        .dz-message {
            margin: 0;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
}

.email-list {
    display: block;
    padding-left: 0;

    .message {
        position: relative;
        display: block;
        height: 3.125rem;
        line-height: 3.125rem;
        cursor: default;
        transition-duration: 0.3s;

        a {
            color: $l-ctl;
        }

        &-single {
            .custom-checkbox {
                margin-top: 2px;
            }

            i {
                color: $muted;
                font-size: 1.125rem;
                padding-left: .4rem;
            }


        }


        &:hover {
            transition-duration: 0.05s;
            background: rgba(152, 166, 173, 0.15);
        }

        .col-mail {
            float: left;
            position: relative;
        }

        .col-mail-1 {
            width: 5.625rem;

            .star-toggle {
                display: block;
                float: left;
                margin-top: 1.125rem;
                font-size: 1rem;
                margin-left: 0.3125rem;
            }

            .email-checkbox {
                display: block;
                float: left;
                margin: 0.9375rem 0.625rem 0 1.25rem;
            }

            .dot {
                display: block;
                float: left;
                border: .4rem solid transparent;
                border-radius: 6.25rem;
                margin: 1.375rem 1.625rem 0;
                height: 0;
                width: 0;
                line-height: 0;
                font-size: 0;
            }
        }

        .col-mail-2 {
            position: absolute;
            top: 0;
            left: 5.625rem;
            right: 0;
            bottom: 0;

            .subject {
                position: absolute;
                top: 0;
                left: 0;
                right: 5.5rem;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
            }

            .date {
                position: absolute;
                top: 0;
                right: 0;
                // width: 4rem;
                // padding-left: 5rem;
            }
        }
    }
}

.email-checkbox {
    cursor: pointer;
    height: 0.9375rem;
    width: 0.9375rem;
    position: relative;
    display: inline-block;
    border-radius: .1rem;
    position: relative;
    top: 0.3125rem;
    box-shadow: inset 0 0 0 .1rem $l-ctl;

    input {
        opacity: 0;
        cursor: pointer;

        &:checked {
            label {
                opacity: 1;
            }
        }
    }

    label {
        position: absolute;
        top: .3rem;
        left: .3rem;
        right: .3rem;
        bottom: .3rem;
        cursor: pointer;
        opacity: 0;
        margin-bottom: 0 !important;
        transition-duration: 0.05s;
    }
}

.mail-list {

    a {
        vertical-align: middle;
        padding: 0.625rem 0.9375rem;
        display: block;
        background: transparent;
        color: $l-ctd;
        font-weight: 600;
    }

    .list-group-item {
        color: #6a707e;
        padding: 0.75rem 1.0625rem;

        i {
            font-size: 1rem;
            padding-right: 0.625rem;
            color: #cccccc;
        }

        &.active {
            color: $white;

            i {
                color: $white;
            }
        }
    }
}

.chat-wrap {
    padding: 1.0625rem 1.875rem;

    .media {

        .media-body {

            h6 {
                font-size: 1.0625rem;
                color: #6a707e;
            }

            p {
                font-size: 0.875rem;
            }
        }
    }
}

.email-filter {
    @media (min-width: 648px) {
        padding-left: 1.25rem;
    }

    @media(min-width: 1700px) {
        padding-left: 1.875rem;
    }

    .input-group {
        &-prepend {
            i {
                font-size: 0.875rem;
                color: $muted;
            }

            .input-group-text {
                border: 0;
                border-bottom: 1px solid #DDDFE1 !important;
                background: transparent;
            }
        }

        .form-control {
            padding: 0 0 0 0.3125rem;
            border: 0;
            font-size: 0.875rem;
            height: 1.875rem;
            color: $muted;
            border-bottom: 1px solid #DDDFE1;

            &::placeholder {
                font-size: 0.875rem;
                color: $muted;
            }
        }

        &>.form-control {
            min-height: 1.875rem;
        }
    }
}

.single-mail {
    display: block;
    padding: 1.5625rem 0;

    .media {
        padding-left: 1.25rem;
        padding-right: 1.25rem;

        @media(min-width: 1700px) {
            padding-left: 1.875rem;
            padding-right: 1.875rem;
        }

        img {
            width: 55px;
            height: 55px;
            border-radius: 50%;
            margin-right: 0.9375rem;

            @media(min-width: 1700px) {
                margin-right: 1.875rem;
            }
        }

        &-body {

            h6 {
                color: #abafb3;
            }

            h4 {
                font-size: 1rem;
                color: #6a707e;

                button {

                    i {
                        font-size: 1.125rem;
                        color: #abafb3;
                        font-weight: bold;
                        transform: rotate(90deg);
                    }
                }
            }

            p {
                font-size: 0.875rem;
                color: #abafb3;
            }
        }
    }

    &.active {
        background: $primary;

        h6,
        h4,
        p,
        i {
            color: $white !important;
        }
    }
}

[direction="rtl"] {
    .email-right-box {
        padding-left: 1rem;
        padding-right: 15rem;

        @include respond('tab-port') {
            padding-left: 1rem;
            padding-right: 12rem;
        }

        @include respond('phone') {
            padding-left: 0;
            padding-right: 0;
        }
    }

    .email-left-box {
        float: right;
    }

    .email-list .message .col-mail-2 {
        right: 5.625rem;
        left: 0;
        float: right;

        .date {
            right: auto;
            left: 0;
        }

        .subject {
            right: 0;
            left: 5.5rem;
        }
    }

}