.footer {
    padding-left: 21.563rem;
    background-color: $primary;

    @include respond('laptop') {
        padding-left: 17rem;
    }

    .copyright {
        padding: 0.9375rem;
        background-color: $body-bg;

        p {
            text-align: center;
            margin: 0;
        }

        a {
            color: $primary;
        }

        @include custommq($min: 991px) {
            border-radius: 0 0 0 50px;
        }
    }
}