.widget-stat {
	.media {
		padding: 0px 0;
		align-items: center;

		&>span {
			height: 85px;
			width: 85px;
			border-radius: 50px;
			padding: 10px 12px;
			font-size: 32px;
			display: flex;
			justify-content: center;
			align-items: center;
			color: $l-ctd;

			@include respond('laptop') {
				height: 70px;
				width: 70px;
				min-width: 70px;
			}
		}

		@include custommq($min: 576px) {}

		.media-body {
			p {
				font-weight: 500;
				font-size: 16px;
				line-height: 1.5;

				@include respond('laptop') {
					font-size: 14px;
				}

				@at-root [data-theme-version="dark"] & {
					color: #c4c9d5;
				}
			}

			small,
			.small {
				font-size: 75%;
			}

			h3 {
				font-size: 30px;
				font-weight: 600;
				margin: 0;
				line-height: 1.2;
			}

			h4 {
				font-size: 24px;
				display: inline-block;
				vertical-align: middle;
			}

			span {
				margin-left: 5px;

			}
		}
	}

	&[class*="bg-"] .media>span {
		background-color: rgba(255, 255, 255, 0.25);
		color: $white;
	}

	&[class*="bg-"] .progress {
		background-color: rgba(255, 255, 255, 0.25) !important;
	}

}

[direction="rtl"] {
	.widget-stat .media .media-body span {
		margin-left: 0;
		margin-right: 10px;
	}
}

.dez-widget-1 {
	.card {
		background: #FFE7DB;

		.card-body {
			p {
				color: #f87533;
			}
		}
	}

	.icon {
		float: right;
		width: 50px;
		height: 50px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 6px;
		font-size: 28px;
	}
}

