.overlay-box {
	position: relative;
	z-index: 1;

	&:after {
		content: "";
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		position: absolute;
		opacity: 0.85;
		background: $primary;
		z-index: -1;
	}
}

