button {
	cursor: pointer;

	&:focus {
		outline: 0;
		box-shadow: none;
	}
}

.btn {
	padding: 0.938rem 1.5rem;
	border-radius: 0;
	font-weight: 500;
	font-size: 1rem;

	&:hover,
	&:focus,
	&:active,
	&.active {
		outline: 0 !important;
	}

	@include respond('laptop') {
		padding: 0.625rem 1rem;
		font-size: 0.813rem;
	}

	&-transparent {
		background-color: transparent;
	}
}

.sharp {
	min-width: 40px;
	padding: 7px;
	height: 40px;
	min-height: 40px;
}

.sharp.btn-xs {
	padding: 3px;
	width: 26px;
	height: 26px;
	min-width: 26px;
	min-height: 26px;
}

.sharp-lg {
	min-width: 56px;
	padding: 15px;
	height: 56px;
	min-height: 56px;
	text-align: center;

	@include respond('laptop') {
		min-width: 40px;
		padding: 7px;
		height: 40px;
		min-height: 40px;
	}
}

.light {

	&.tp-btn {
		background-color: transparent;
	}

	&.btn-default {
		background: #ADB6C7;
	}

	&.btn-success {
		background-color: $success-light;
		border-color: $success-light;
		color: $success;

		g [fill] {
			fill: $success;
		}

		@at-root [data-theme-version="dark"] & {
			background-color: $success-opacity;
			border-color: transparent;
		}

		&:hover {
			background-color: $success;
			border-color: $success;
			color: #fff;

			g [fill] {
				fill: $white;
			}
		}
	}

	&.btn-info {
		background-color: $info-light;
		border-color: $info-light;
		color: $info;

		g [fill] {
