.btn-light {
	background: $light;
	border-color: $light;


	&:active,
	&:focus,
	&:hover {
		background: lighten($light, 25%);
		border-color: lighten($light, 25%);
	}
}

.btn-outline-light {
	color: $dark;

	@at-root [data-theme-version="dark"] & {
		color: $body-color;
	}
}

.btn-outline-secondary {
	@at-root [data-theme-version="dark"] & {
		color: $body-color;
	}
}

.btn-outline-dark {
	@at-root [data-theme-version="dark"] & {
		color: $body-color;
	}
}

.btn-dark {
	background: $dark;
	border-color: $dark;
	color: $white;


	&:active,
	&:focus,
	&:hover {
		background: darken($dark, 10%);
		color: $white;
		border-color: darken($dark, 10%);
	}
}

.btn-group {

	&.btn-rounded {

		.btn:first-child {
			border-top-left-radius: 30px;
			border-bottom-left-radius: 30px;
		}

		.btn:last-child {
			border-top-right-radius: 30px;
			border-bottom-right-radius: 30px;
		}
	}
}



